<template>
  <div>
    <div class="lds-ring" v-if="loader"><span id="loaderText">Loading</span><div></div><div></div><div></div><div></div></div>
    <!-- Career Section End -->
    <!-- Portfolio Section Start -->
    <div id="portfolioSection" @mouseover="portfolioHover(true)" @mouseout="portfolioHover(false)" v-if="dataLoadSignal">
      <b-container>
        <div class="portfolio-cards">
          <b-row v-for="row in formattedClubsPortfolio" :key="row" class="portfolio-row">
            <b-col cols="12" sm="12" md="" lg="" v-for="club in row" :key="club">
              <div class="card">
                <img :src="club.img">
              </div>
              <div class="portfolio-details">
                <b-row>
                  <b-col lg="2" md="2" sm="12">
                    <h2>{{ club.name }}</h2>
                    <p>{{ club.lang }}</p>
                  </b-col>
                  <b-col lg="10" md="10" sm="12">
                    <div class="description" v-for="item in club.description" :key="item">
                      <div><a :href="item.live">LINK - {{item.live}}</a></div>
                      <div><a :href="item.git">GITHUB - {{item.git}}</a></div>
                    </div>
                  </b-col>
                </b-row>
              </div>
            </b-col>
          </b-row>
        </div>
      </b-container>
    </div>
    <!-- Portfolio Section End -->
  </div>
</template>

<script>
import { db } from '../firebase.js';
export default {
  data() {
    return {
      jobRetriveData: [],
      loader: true,
      dataLoadSignal: false,
      portfolioData: [
        { name:'RentByOwner', lang: 'VUE', description: [{ live: '', git: ''}], img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/rbo.png?alt=media&token=7881a7dd-021e-4570-90a3-e561e1039711'},
        { name: 'CareerKi', lang: 'VUE', description: [{ live: '', git: ''}], img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/careerki.png?alt=media&token=6bdfac98-45a6-4dc3-9880-c88a6c0c050e'},
        { name:'Job Lab', lang: 'VUE', description: [{ live: 'https://job-lab.github.io/', git: 'https://github.com/programmerdeb7/JOB_APPLICATION'}], img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/joblab.jpg?alt=media&token=1fd92bbc-9548-4357-a562-a53ca2ac5044'},
        { name: 'ID Card', lang: 'PHP', description: [{ live: '', git: 'https://github.com/programmerdeb7/SOFTWARE_ENG_LAB'}], img: 'https://firebasestorage.googleapis.com/v0/b/portfolio-62af9.appspot.com/o/just.png?alt=media&token=5c13b8b6-9fee-4e46-a7eb-85ae6059bb8d'}
      ],
    }
  },
  firestore(){
    return {
      jobRetriveData: db.collection('jobs').orderBy('createdAt','asc')
    }
  },
  methods: {
    portfolioHover: function(value){
      if(value == true){
        this.$emit('portfolio', true);
      }
      if(value == false){
        this.$emit('portfolio', false);
      }
    }
  },
  computed: {
    formattedClubsPortfolio() {
        return this.portfolioData.reduce((c, n, i) => {
          if (i % 2 === 0) c.push([]);
          c[c.length - 1].push(n);
          return c;
        }, []);
    }
  },
  watch: {
    'jobRetriveData': function(){
      if(this.jobRetriveData.length > 1){
        this.loader = false;
        this.dataLoadSignal = true;
      }
    }
  }
}
</script>
<style scoped>
#portfolioSection{
  margin-top: 0px;
}
.portfolio-row{
  margin-top: 25px;
}
</style>



